@import url('./reset.css');
@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@400;500&display=swap');

*{box-sizing: border-box;}
body {
  background: #4e62fe;
  background: -moz-linear-gradient(-45deg, #4e62fe 0%, #4589e2 50%, #4e62fe 100%);
  background: -webkit-linear-gradient(-45deg, #4e62fe 0%, #4589e2 50%, #4e62fe 100%);
  background: linear-gradient(135deg, #4e62fe 0%, #4589e2 50%, #4e62fe 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#4e62fe', endColorstr='#4e62fe', GradientType=1);

  font-family: 'Rubik', sans-serif;
  margin: 0;
  display: flex;
  place-items: center;
  min-width: 320px;
  min-height: 100vh;
}
button {
  color: #fff;
  cursor: pointer;
}
#root {
  display: flex;
  justify-content: center;
  width: 100%;
}

.container {
  background-color: #011024;
  color: #fff;
  border-radius: 20px;
  padding: 5px;
  min-width: 345px;
  position: relative;
}

.adorno {
  position: absolute;
  background-color: #06387e;
  bottom: 147px;
  height: 20px;
  left: 10%;
  right: 10%;
  z-index: 0;
  border-radius: 0 0 20px 20px;

}

.top-menu {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  
  z-index: 1;
}

.coords-display {
  opacity: 1;
  position: absolute;

  left: 15px;
  top: -330px;
  right: 15px;

  border-radius: 5px 5px 20px 20px;

  color: #011024;
  -webkit-box-shadow: 0 20px 50px 0 rgba(0, 0, 255, .5);
  box-shadow: 0 20px 50px 0 rgba(0, 0, 255, .5);

  -webkit-transition: all .3s ease;
  -moz-transition: all .3s ease;
  -ms-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
}

.carousel {
  width: 335px;
  overflow: hidden;

  background: #126ef3;
  background: -moz-linear-gradient(45deg, #126ef3 0%, #15c3f6 100%);
  background: -webkit-linear-gradient(45deg, #126ef3 0%, #15c3f6 100%);
  background: linear-gradient(45deg, #126ef3 0%, #15c3f6 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#126ef3', endColorstr='#15c3f6', GradientType=1);

  border-radius: 15px;
  padding: 20px;

  -webkit-box-shadow: 0 20px 50px 0 rgba(0, 0, 255, .5);
  box-shadow: 0 20px 50px 0 rgba(0, 0, 255, .5);

  text-align: center;
}
.inner {
  display: flex;
  -webkit-transition: all .3s ease;
-moz-transition: all .3s ease;
-ms-transition: all .3s ease;
-o-transition: all .3s ease;
transition: all .3s ease;
}

.carousel-item > div {
  width: 295px;
}

.principal-display {
  
  position: relative;
  z-index: 1;
  overflow: hidden;
}

.principal-display.noche .carousel{
  background: #020f47;
  background: -moz-linear-gradient(45deg, #040490 0%, #020f47 100%);
  background: -webkit-linear-gradient(45deg, #040490 0%, #020f47 100%);
  background: linear-gradient(45deg, #040490 0%, #020f47 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#040490', endColorstr='#020f47', GradientType=1);
}

span.material-symbols-outlined {
  font-size: 1em;
  opacity: .7;

}
.image-icon{
  margin-top: -5px;
}
.icono-grande {
  font-size: 7em !important;
  height: 195px;
  width: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
}
.temp {
  font-size: 6em;
  line-height: 1em;
  margin-top: -30px;
}

span.temp-symbol {
  position: absolute;
  line-height: 1.1em;
  font-size: 0.4em;
  opacity: 0.8;
  z-index: -1;
}

.description {
  text-transform: uppercase;
  font-size: 0.8em;
  letter-spacing: 1px;
}

.fecha {
  font-size: 0.7em;
  opacity: .7;
}

.temp-mix-max-container {
  display: flex;
  justify-content: space-around;;
  margin-top: 20px;
  font-size: 1.4em;
}

.temp-mix-max-container div {
  display: flex;
  align-items: center;
}
span.symbol-leyenda {
  font-size: 0.6em;
  opacity: .7;
  line-height: 12px;
}
span.dato {
  margin: 0 5px;
}

.menu-container {
  padding: 30px 0 20px;
}

.menu {
  display: flex;
  justify-content: space-between;
  text-align: center;
  align-items: flex-start;
  list-style: none;
  margin: 0;
  padding: 0;
}
.marker {
  width: 70px;
  height: 98px;
  position: absolute;
  background: #1382f4;
  bottom: 20px;
  border-radius: 15px;
  -webkit-transition: all .3s ease;
  -moz-transition: all .3s ease;
  -ms-transition: all .3s ease;
  -o-transition: all .3s ease;
  z-index: -1;
  transition: all .3s ease;

  -webkit-box-shadow: 0 0 10px 0 #1382f48f;
box-shadow: 0 0 10px 0 #1382f48f;
}
.menu-item {
  flex: 1 1 0;
}

.menu-item button {
  background-color: transparent;
  border: solid 1px #09264d;
  border-radius: 15px;
  padding: 20px 0px;
  width: 84%;
  margin: auto;
  cursor: pointer;
  font-size: 1em;
}

.menu-item button:hover {
  border-color: #123b71;
}

.menu-item-icon {
  font-size: 1.4em;
  line-height: 1em;
}

.menu-item-leyenda {
  font-size: 0.6em;
  opacity: .7;
  text-transform: uppercase;
  letter-spacing: 1px;
  line-height: 1em;
  margin-top: 3px;
}

.footer {
  text-align: center;
  font-size: .8em;
  margin-top: 10px;
  color: #0423AE
}

.footer a {
  color: #0423AE;
  text-decoration: underline;
}

.footer a:hover {
  color: #0423AE;
  text-decoration: none;
}